Оформить</v-btn>\n          </v-card-actions>\n        </v-card>\n      </v-dialog>\n\n    </v-layout>\n  </v-container>\n</template>\n\n<script>\n  import Scrollbar from 'smooth-scrollbar';\n\n  export default {\n    data() {\n      return {\n        dialogAdded: false,\n      };\n    },\n    computed: {\n      user() {\n        return this.$store.state.user;\n      },\n      cards() {\n        return this.$store.state.eps.cards;\n      },\n    },\n    mounted() {\n      this.GetCards();\n      Scrollbar.initAll();\n    },\n    methods: {\n      GetCards() {\n        const app = this;\n        app.$store.getters.ax.post('profiles/api/cards/list', { group_id: app.user.group_id })\n          .then((response) => {\n            app.$store.commit('eps/SetCards', response.data.cards);\n            Scrollbar.initAll();\n          });\n      },\n      Add(card) {\n        const app = this;\n        if (card.nominal === undefined || !card.nominal) {\n          document.getElementById(`select-card-${card.id}`).click();\n          return;\n        }\n        app.$store.dispatch('eps/AddToCart', card);\n        app.dialogAdded = true;\n        card.nominal = null;\n      },\n      GoToCart() {\n        this.$router.push('/eps-cart');\n      },\n    },\n  };\n</script>\n\n<style scoped>\n  #app .card-description {\n    position:    relative;\n    height:      120px;\n    text-align:  justify;\n    overflow-y:  scroll;\n    font-size:   13px;\n    line-height: 15px;\n  }\n  #app h3 {\n    margin-bottom: 5px;\n    text-align:    center;\n  }\n</style>\n","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n#app .card-description {\n  position:    relative;\n  height:      120px;\n  text-align:  justify;\n  overflow-y:  scroll;\n  font-size:   13px;\n  line-height: 15px;\n}\n#app h3 {\n  margin-bottom: 5px;\n  text-align:    center;\n}\n"],"sourceRoot":""}]);

